"use client";

import { useEffect, useState } from "react";
import Image from "next/image";
import { useParams } from "next/navigation";
import { Play, Plus, Star, Calendar, Clock, X, Info } from "lucide-react";
import { getMovieDetails } from "@/api/tmdb";
import { MovieCard } from "@/components/MovieCard";
import YouTube from "react-youtube";

export default function MovieDetailsPage() {
    const { id } = useParams();
    const [movie, setMovie] = useState<any>(null);
    const [loading, setLoading] = useState(true);
    const [showTrailer, setShowTrailer] = useState(false);

    useEffect(() => {
        async function loadDetails() {
            if (id) {
                const data = await getMovieDetails(id as string);
                setMovie(data);
                setLoading(false);
            }
        }
        loadDetails();
    }, [id]);

    if (loading) return <div className="min-h-screen bg-black flex items-center justify-center text-white">Loading...</div>;
    if (!movie) return <div className="min-h-screen bg-black flex items-center justify-center text-white">Movie not found</div>;

    const trailer = movie.videos?.results.find((v: any) => v.type === "Trailer" && v.site === "YouTube");
    const crew = movie.credits?.crew.slice(0, 5);
    const cast = movie.credits?.cast.slice(0, 10);
    const reviews = movie.reviews?.results.slice(0, 3);
    const similar = movie.similar?.results.slice(0, 5);

    return (
        <main className="min-h-screen bg-bg-main pb-20">
            {/* Cinematic Background */}
            <div className="relative h-[80vh] w-full overflow-hidden">
                {/* Video Player Overlay */}
                {showTrailer && trailer ? (
                    <div className="fixed inset-0 z-50 bg-black flex items-center justify-center">
                        <button
                            onClick={() => setShowTrailer(false)}
                            className="absolute top-8 right-8 text-white hover:text-red-500 z-50"
                        >
                            <X size={32} />
                        </button>
                        <YouTube
                            videoId={trailer.key}
                            opts={{
                                width: '100%',
                                height: '100%',
                                playerVars: { autoplay: 1 }
                            }}
                            className="w-full h-full max-w-6xl max-h-[80vh] aspect-video"
                        />
                    </div>
                ) : (
                    <>
                        <Image
                            src={`https://image.tmdb.org/t/p/original${movie.backdrop_path}`}
                            alt={movie.title}
                            fill
                            className="object-cover"
                            priority
                        />
                        <div className="absolute inset-0 bg-gradient-to-t from-bg-main via-bg-main/60 to-transparent" />
                        <div className="absolute inset-0 bg-gradient-to-r from-bg-main via-bg-main/40 to-transparent" />
                    </>
                )}
            </div>

            <div className="container mx-auto px-6 lg:px-20 -mt-[40vh] relative z-10">
                <div className="flex flex-col lg:flex-row gap-12">
                    {/* Poster */}
                    <div className="w-full max-w-[300px] shrink-0 mx-auto lg:mx-0">
                        <div className="aspect-[2/3] rounded-2xl overflow-hidden shadow-2xl border border-white/10 relative group">
                            <Image
                                src={`https://image.tmdb.org/t/p/w780${movie.poster_path}`}
                                alt={movie.title}
                                fill
                                className="object-cover"
                            />
                        </div>
                    </div>

                    {/* Content */}
                    <div className="flex-1 space-y-8 pt-10">
                        <div>
                            <h1 className="text-4xl lg:text-6xl font-display font-bold text-white mb-4 leading-tight">
                                {movie.title}
                            </h1>
                            <p className="text-xl text-text-secondary italic font-light mb-6">"{movie.tagline}"</p>

                            <div className="flex flex-wrap items-center gap-6 text-sm text-text-muted mb-6">
                                <span className="flex items-center gap-2 text-accent-primary font-bold">
                                    <Star size={16} fill="currentColor" />
                                    {movie.vote_average.toFixed(1)}
                                </span>
                                <span className="flex items-center gap-2">
                                    <Calendar size={16} />
                                    {movie.release_date.split("-")[0]}
                                </span>
                                <span className="flex items-center gap-2">
                                    <Clock size={16} />
                                    {Math.floor(movie.runtime / 60)}h {movie.runtime % 60}m
                                </span>
                                <div className="flex gap-2">
                                    {movie.genres.map((g: any) => (
                                        <span key={g.id} className="px-3 py-1 rounded-full border border-white/10 bg-white/5 text-xs text-white">
                                            {g.name}
                                        </span>
                                    ))}
                                </div>
                            </div>

                            {/* Actions */}
                            <div className="flex items-center gap-4">
                                {trailer && (
                                    <button
                                        onClick={() => setShowTrailer(true)}
                                        className="flex items-center gap-2 px-8 py-4 bg-accent-primary hover:bg-accent-primary/90 text-white font-bold rounded-xl transition-all shadow-[0_0_20px_rgba(229,9,20,0.4)]"
                                    >
                                        <Play fill="currentColor" size={20} />
                                        Watch Trailer
                                    </button>
                                )}
                                <button className="flex items-center gap-2 px-6 py-4 bg-white/10 hover:bg-white/20 border border-white/10 backdrop-blur-md text-white font-medium rounded-xl transition-all">
                                    <Plus size={20} />
                                    Add to Watchlist
                                </button>
                            </div>
                        </div>

                        {/* Overview */}
                        <div>
                            <h3 className="text-lg font-bold text-white mb-2">Synopsis</h3>
                            <p className="text-text-secondary leading-relaxed text-lg max-w-3xl">
                                {movie.overview}
                            </p>
                        </div>

                        {/* Cast */}
                        <div>
                            <h3 className="text-lg font-bold text-white mb-4">Top Cast</h3>
                            <div className="flex gap-4 overflow-x-auto hide-scrollbar pb-4">
                                {cast && cast.length > 0 ? cast.map((actor: any) => (
                                    <div key={actor.id} className="min-w-[120px] text-center">
                                        <div className="w-24 h-24 mx-auto rounded-full overflow-hidden border-2 border-white/10 mb-2 relative">
                                            {actor.profile_path ? (
                                                <Image
                                                    src={`https://image.tmdb.org/t/p/w200${actor.profile_path}`}
                                                    alt={actor.name}
                                                    fill
                                                    className="object-cover"
                                                />
                                            ) : (
                                                <div className="w-full h-full bg-neutral-800 flex items-center justify-center text-xs">No Image</div>
                                            )}
                                        </div>
                                        <p className="text-white text-sm font-medium truncate">{actor.name}</p>
                                        <p className="text-text-muted text-xs truncate">{actor.character}</p>
                                    </div>
                                )) : <p className="text-text-secondary">No cast information available.</p>}
                            </div>
                        </div>
                    </div>
                </div>

                {/* Reviews & More */}
                <div className="mt-20 grid grid-cols-1 lg:grid-cols-3 gap-12 border-t border-white/5 pt-12">
                    <div className="lg:col-span-2 space-y-8">
                        <h3 className="text-2xl font-display font-bold text-white">Reviews</h3>
                        {reviews && reviews.length > 0 ? (
                            <div className="space-y-6">
                                {reviews.map((review: any) => (
                                    <div key={review.id} className="bg-bg-card p-6 rounded-xl border border-white/5">
                                        <div className="flex items-center justify-between mb-4">
                                            <h4 className="font-bold text-white">{review.author}</h4>
                                            <span className="text-xs text-text-muted">{new Date(review.created_at).toLocaleDateString()}</span>
                                        </div>
                                        <p className="text-text-secondary line-clamp-4 leading-relaxed">{review.content}</p>
                                    </div>
                                ))}
                            </div>
                        ) : (
                            <p className="text-text-secondary italic">No reviews yet.</p>
                        )}
                    </div>

                    <div className="space-y-6">
                        <h3 className="text-xl font-display font-bold text-white">Similar Movies</h3>
                        <div className="space-y-4">
                            {similar && similar.length > 0 ? similar.map((m: any) => (
                                <div key={m.id} className="flex gap-4 group cursor-pointer">
                                    <div className="w-20 aspect-[2/3] relative rounded bg-neutral-800 overflow-hidden">
                                        {m.poster_path && (
                                            <Image
                                                src={`https://image.tmdb.org/t/p/w200${m.poster_path}`}
                                                alt={m.title}
                                                fill
                                                className="object-cover group-hover:scale-105 transition-transform"
                                            />
                                        )}
                                    </div>
                                    <div>
                                        <h4 className="text-white font-medium group-hover:text-accent-primary transition-colors line-clamp-1">{m.title}</h4>
                                        <p className="text-text-muted text-sm">{m.release_date?.split("-")[0]}</p>
                                        <div className="flex items-center gap-1 text-accent-primary text-xs mt-1">
                                            <Star size={12} fill="currentColor" /> {m.vote_average.toFixed(1)}
                                        </div>
                                    </div>
                                </div>
                            )) : <p className="text-text-secondary">No similar movies found.</p>}
                        </div>
                    </div>
                </div>
            </div>
        </main>
    );
}
